
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import value2name from 'vux/src/filters/value2name.js'


import appfooter from "../element/footer.vue"
export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
XInput,
XAddress,
XButton,
Cell
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "添加客户",
address: "选择地址",
username:"",
userphone:"",
selectAddress: "",
value_0_1: [],
value: [],
title2: '设置值',
value2: ['天津市', '市辖区', '和平区'],
value3: ['广东省', '中山市', '--'],
addressData: ChinaAddressV4Data,
value4: [],
value5: ['广东省', '深圳 市', '南山区'],
showAddress: false
}
},
created() {

},
methods: { //初始化事件
doShowAddress() {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange(ids, names) {

console.log(names);
this.selectAddress = names[0] + names[1] + names[2];
},
changeData() {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels() {
this.value2 = ['广东省', '广州市', '天河区']
},
changeDataByLabels2() {
this.value2 = ['广东省', '中山市', '--']
},
getName(value) {
return value2name(value, ChinaAddressV4Data)
},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
},onAddClint:function(){
let params = {
openid: this.openid
}
this.$axios.post(this.umcons.serviceAddress.ADD_CLIENT, params).then(function(response) {
this.pageinfo = response.data.result;
console.log(response.data.result);
}.bind(this));
}

}
}



