
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import appfooter from "./element/footer.vue"
export default {
name: 'page',
components: {
appfooter,
XHeader,
Group,
Search,
Cell,
Grid,
GridItem,
GroupTitle,
TransferDom
},
data() {
return {
msg: 'Welcome to Your Vue.js App',
title: "工作台",
results: [],
value: '',
top
}
},
created() {

},
methods: { //初始化事件
resultClick(item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult(val) {
console.log('on-change', val)
this.results = val ? getResult(this.value) : []
},
onSubmit() {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus() {
console.log('on focus')
},
onCancel() {
console.log('on cancel')
}
}

}

function getResult(val) {
let rs = []
for(let i = 0; i < 5; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



