import Vue from 'vue'
import Router from 'vue-router'
//首页
import Home from '@/components/Home'
//消息
import Message from '@/components/message/Message'
//工作相关
import Work from '@/components/work/Work'
//添加客户
import AddClient from '@/components/work/AddClient'
//添加跟进
import AddFollowUp from '@/components/work/AddFollowUp'
//添加任务
import AddTask from '@/components/work/AddTask'
//添加订单
import AddOrder from '@/components/work/AddOrder'
//添加日志
import AddRecord from '@/components/work/AddRecord'
//添加项目
import AddProject from '@/components/work/AddProject'
//添加设备
import AddDevice from '@/components/work/AddDevice'
//待办任务
import LoadTask from '@/components/work/LoadTask'
//添加设备
import RecordManage from '@/components/work/RecordManage'
//添加设备
import ScheduleRemind from '@/components/work/ScheduleRemind'
//通讯录
import Contacts from '@/components/contacts/Contacts'
//通讯录详情
import ContactsInfo from '@/components/contacts/ContactsInfo'
//统计
import Statistics from '@/components/statistics/Statistics'
//其他
import Other from '@/components/other/Other'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },{
      path: '/Message',
      name: 'Message',
      component: Message
    },{
      path: '/Statistics',
      name: 'Statistics',
      component: Statistics
    },{
      path: '/Contacts',
      name: 'Contacts',
      component: Contacts
    },{
      path: '/Other',
      name: 'Other',
      component: Other
    },{
      path: '/ContactsInfo',
      name: 'ContactsInfo',
      component: ContactsInfo
    },{
      path: '/AddClient',
      name: 'AddClient',
      component: AddClient
    },{
      path: '/AddFollowUp',
      name: 'AddFollowUp',
      component: AddFollowUp
    },{
      path: '/AddTask',
      name: 'AddTask',
      component: AddTask
    },{
      path: '/AddOrder',
      name: 'AddOrder',
      component: AddOrder
    },{
      path: '/AddRecord',
      name: 'AddRecord',
      component: AddRecord
    },{
      path: '/AddProject',
      name: 'AddProject',
      component: AddProject 
    },{
      path: '/AddDevice',
      name: 'AddDevice',
      component: AddDevice
    },{
      path: '/LoadTask',
      name: 'LoadTask',
      component: LoadTask
    },{
      path: '/RecordManage',
      name: 'RecordManage',
      component: RecordManage 
    },{
      path: '/ScheduleRemind',
      name: 'ScheduleRemind',
      component: ScheduleRemind
    }
  ]
})



